@import"https://fonts.googleapis.com/css2?family=Ubuntu+Mono:wght@400;700&display=swap";.result--container{font-family:"Ubuntu Mono",monospace;width:100%}.result--limiter{width:100%;margin:0 auto;margin-bottom:50px}.result-header{display:flex;justify-content:space-between;align-items:center;background-color:#f0f0f0;padding:10px;border-radius:6px 6px 0 0;border:1px solid #e1e4e8;margin-bottom:10px}.result-header__title{font-size:1.4rem}.result-header__copybtn{font-size:1.8rem;transition:all .2s ease-in-out;background:transparent;padding:10px 15px;border:1px solid #e1e4e8;border-radius:6px}.result-header__copybtn::after{font-family:"Font Awesome 5 Free";content:"";padding-left:5px}.result-header__copybtn:hover{background-color:#e6e6e6}.result{border-left:1px solid #e1e4e8;border-right:1px solid #e1e4e8;border-bottom:1px solid #e1e4e8;font-size:1.6rem;border-radius:0 0 6px 6px;background:#fff;padding-top:6px;width:100%}.result__string{border-bottom:1px solid #e1e4e8}.result__line{border-right:1px solid #e1e4e8;width:5%;color:#a0a0a0;text-align:right;padding-right:4px}.result__text{width:95%;padding-left:4px}