.shell-window{margin:0 auto;border-right:1px solid #1f1f1f;border-left:1px solid #1f1f1f;background:transparent;width:576px;min-height:337px;padding:4px 6px 2px 6px;font-size:1.4rem;line-height:1}.shell-window-header{color:#b4b4b4;font-size:1rem;font-weight:300;border-bottom:1px solid #1f1f1f}.shell-window .prompt{background:transparent}.shell-window .prompt__name{color:#fff}.test--line{font-size:1.4rem;color:#fff;font-weight:400}.test-typing{animation:test2222 .3s steps(100);overflow:hidden;white-space:nowrap;color:aqua;font-size:1.4rem;transition-delay:5s}@keyframes test2222{0%{width:0ch}100%{width:60ch}}